body{
    padding: 0;
    margin: 0;
}

.home-inner{
    position: relative;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(165, 55, 253, 0.5)), to(rgba(229,178,202,0.7))), url(/koladeore/internship-website/tree/develop/website/src/static/media/bible_image.30a77886.jpg);
    background-image: -webkit-linear-gradient(left, rgba(165, 55, 253, 0.5) 0%, rgba(229,178,202,0.7) 100%), url(/koladeore/internship-website/tree/develop/website/src/static/media/bible_image.30a77886.jpg);
    background-image: linear-gradient(to right, rgba(165, 55, 253, 0.5) 0%, rgba(229,178,202,0.7) 100%), url(/koladeore/internship-website/tree/develop/website/src/static/media/bible_image.30a77886.jpg);
    background-size: cover;
    background-repeat: none;
    background-position: center;
    margin: auto;
    min-height: 100vh;
}

.text{
    overflow-x: hidden;
    font-family: 'Lato', sans-serif;
    color: #ffffff;
    position: absolute;
    top: 300px;
}
.text h1,h3{
    font-size: 4rem;
    display: block;
    text-align: center;
    letter-spacing: 5px;
    -webkit-animation: moveInLeft 1s ease-out;
            animation: moveInLeft 1s ease-out;
    /* animation-name: moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out; */
    /* animation-iteration-count: 3;
    animation-delay: 3s; */
}
@-webkit-keyframes moveInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px) 
    }
    80%{
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
@keyframes moveInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px) 
    }
    80%{
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
.sunday {
