.img{ 
    padding: 20px;
}
.image{
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(165, 55, 253, 0.5)), to(rgba(229,178,202,0.7)));
    background-image: -webkit-linear-gradient(left, rgba(165, 55, 253, 0.5) 0%, rgba(229,178,202,0.7) 100%);
    background-image: linear-gradient(to right, rgba(165, 55, 253, 0.5) 0%, rgba(229,178,202,0.7) 100%);
